import React, { Component } from 'react';
import PartnerForm from './PartnerForm.js';
import Utils from './Utils.js';

const JSON_URL = "https://s3.amazonaws.com/static.mozio.com/mobile/sdk/demo/partners.json";
class PartnerPage extends Component {
    constructor(props) {
        super(props);
        this.utils = new Utils();
        this.state = {
            partners: [],
            loading: false
        };

        this.onSubmit = this.onSubmit.bind(this);
    }

    async getJsonPartners() {
        var response = await fetch(JSON_URL);
        return response.json()
    }

    componentDidMount() {
        this.getJsonPartners()
            .then(data => {
                console.log(data)
                this.setState({
                    partners: data
                })
            });
    }

    onSubmit(data, files) {
        if (!this.validateOrAbort(data)) { return; }

        this.setState({
            loading: true
        });

        var github = this.props.github;
        var repo = github.getRepo("mozioinc", "sdk_demos");
        var branchName = "demo-" + data.id;
        var logoImageName = "logo_" + data.id + ".png";
        var screenImageName = data.id + "_narrow.png";
        var screenWideImageName = data.id + "_wide.png";

        // 1. Create the new branch:
        repo.createBranch("master", branchName)
            .then(async () => {
                // 2. Write the logo image
                const base64FileString = await this.utils.readFileAsBas64String(files.logo);
                await repo.writeFile(branchName, "images/"+logoImageName, base64FileString, "Add " + data.name + " demo logo", {
                    encode: false // We shouldn't encode the file to base64 - it's already base64 from utils above
                });
                return repo;
            })
            .then(async (repo) => {
                // 3. Write the screen image
                const base64FileString = await this.utils.readFileAsBas64String(files.screenImage);
                await repo.writeFile(branchName, "images/"+screenImageName, base64FileString, "Add " + data.name + " demo screen image", {
                    encode: false // We shouldn't encode the file to base64 - it's already base64 from utils above
                });
                return repo;
            })
            .then(async (repo) => {
                // 4. Write the wide screen image
                const base64FileString = await this.utils.readFileAsBas64String(files.screenWideImage);
                await repo.writeFile(branchName, "images/"+screenWideImageName, base64FileString, "Add " + data.name + " demo screen wide image", {
                    encode: false // We shouldn't encode the file to base64 - it's already base64 from utils above
                });
                return repo;
            })
            .then(async (repo) => {
                // 5. Write the JSON file:
                data.logo_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/master/images/"+logoImageName;
                data.screen_image_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/master/images/"+screenImageName;
                data.screen_wide_image_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/master/images/"+screenWideImageName;
                var changed = false
                var newArray = this.state.partners.map(function (partner) {
                    if (partner.id === data.id) {
                        // Found a partner with the same ID. Updating it:
                        changed = true
                        return data
                    } else return partner;
                });
                if (!changed) {
                    // No partner with the same ID, so simply add it:
                    newArray.push(data)
                }
                // We create the JSON string and then encode it to base64 (_very_ rudimentary obfuscation)
                var fileContents = btoa(JSON.stringify(newArray))
                await repo.writeFile(branchName, "demos.json", fileContents, "Add " + data.name + " to demos JSON", {
                    encode: true // The JSON has been base64 encoded above, but we do it again because we want it to stay encoded in the repo
                })
                return repo;
            })
            .then((repo) => {
                // 6. Create the PR into master:
                return repo.createPullRequest({
                    title: data.name,
                    head: branchName,
                    base: "master",
                    body: "Adds all necessary files for the " + data.name + " demo: \n" +
                        "```\n" + JSON.stringify(data, null, 2) + "\n```"
                });
            })
            .then((result) => {
                alert("Success!");
                this.setState({
                    loading: false
                });
            }, (err) => {
                alert("Error... " + err);
                this.setState({
                    loading: false
                });

                // Something went wrong - so we clean up by removing the branch:
                return repo.deleteRef("heads/"+branchName).then(() => {}, (err) => {
                    alert("Could not delete the branch. It probably failed before a new branch was created: " + err);
                });
            });
    }

    validateOrAbort(data) {
        if (!data.id || data.id === "") {
            alert("Please fill in the ID!");
            return false;
        }
        if (!data.tint_color || data.tint_color === "") {
            alert("Please fill in the tint color!");
            return false;
        }
        if (!data.contrast_tint_color || data.contrast_tint_color === "") {
            alert("Please fill in the contrast tint color!");
            return false;
        }
        if (!data.primary_color || data.primary_color === "") {
            alert("Please fill in the primary color!");
            return false;
        }
        return true;
    }

    render() {
        return (
            <React.Fragment>
                <ul className="column">
                    {this.state.partners.map(partner =>
                        <li key={partner.id}>
                            <p>{partner.name}</p>
                            <img className="partner-logo" src={partner.logo_url} alt={partner.name} />
                        </li>
                    )}
                </ul>
                {this.state.loading || <div className="column form">
                    <PartnerForm
                        onSubmit={this.onSubmit}
                    />
                </div>}
                {this.state.loading && <p>Saving...</p>}
            </React.Fragment>
        );
    }
}
export default PartnerPage;